import React from 'react'
import './footer.css';
import tiktok from '../../assets/tiktok.png';
import reddit from '../../assets/reddit.png';
import instagram from '../../assets/instagram.png';

const Footer = () => {
  return (
    <div className='mc__footer'>
        <div className='mc__footer-container'>
            <div className='mc__footer-links'>
            <a href="https://www.tiktok.com/@mythoscraft?lang=en"><img src={tiktok} alt="Follow us on our Tiktok!"/></a>
            <a href="https://www.reddit.com/r/MythosCraftServer/"><img src={reddit} alt="Join our Reddit community!"/></a>
            <a href="https://www.instagram.com/mythos_mc/?hl=en"><img src={instagram} alt="Follow us on Instagram!"/></a>

            </div>
        </div>
    </div>
  )
}

export default Footer