import { useEffect, useState } from "react";
import {
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  onAuthStateChanged,
  signOut,
} from "firebase/auth";
import { useNavigate } from "react-router-dom";
import { auth } from "../../firebase-config";
import "./adminlogin.css";

function AdminLogin() {
  let navigate = useNavigate(); 
  const [registerEmail, setRegisterEmail] = useState("");
  const [registerPassword, setRegisterPassword] = useState("");
  const [loginEmail, setLoginEmail] = useState("");
  const [loginPassword, setLoginPassword] = useState("");

  const [user, setUser] = useState({});
  useEffect(() => {
    onAuthStateChanged(auth, (currentUser) => {
        setUser(currentUser);
    });
  },[])

  const register = async () => {
    try {
      const user = await createUserWithEmailAndPassword(
        auth,
        registerEmail,
        registerPassword
      );
      console.log(user);
    } catch (error) {
      console.log(error.message);
    }
  };

  const login = async () => {
    try {
      const user = await signInWithEmailAndPassword(
        auth,
        loginEmail,
        loginPassword
      );
      console.log(user);
      navigate('../admin');
    } catch (error) {
      console.log(error.message);
    }
  };



  const logout = async () => {
    await signOut(auth);
  };

  return (
    <div className="mc__login">
      <div className="mc__login-content">
        <h1>Login To Continue</h1>
        <div className="mc__login-content_register">
          <h3> Register User </h3>
          <input
            placeholder="Email..."
            onChange={(event) => {
              setRegisterEmail(event.target.value);
            }}
          />
          <input
            placeholder="Password..."
            onChange={(event) => {
              setRegisterPassword(event.target.value);
            }}
          />

          <button onClick={register}> Create User</button>
        </div>

        <div className="mc__login-content_login">
          <div className="mc__login-content_login-input">
            <h3> Username or Email </h3>
            <input
              placeholder="Email..."
              onChange={(event) => {
                setLoginEmail(event.target.value);
              }}
            />
            <h3> Password </h3>
            <input
              placeholder="Password..."
              onChange={(event) => {
                setLoginPassword(event.target.value);
              }}
            />
          </div>
          <div className="mc__login-content_login-button">
            <button onClick={login}> Login </button>
          </div>
        </div>
        <div className="mc__login-content_logged">
          <h4> User Logged In: </h4>
          {user ?user.email: "Not logged in"}

          <button onClick={logout}> Sign Out </button>
        </div>
      </div>
    </div>
  );
}

export default AdminLogin;