import React, { useState } from 'react';
import { RiMenu3Line, RiCloseLine} from 'react-icons/ri';
import logo from '../../assets/logo.png';
import discord from '../../assets/discord.png';

import './navbar.css';

const Menu = () => (
  <>
  <p><a href="../">Home</a></p>
  <p><a href="/#play/">Play</a></p>
  <p><a href="https://store.mythosmc.com" target="_blank" rel="noreferrer">Store</a></p>
  <p><a href="https://mythoscraftmc.fandom.com/wiki/MythosCraft_Wiki" target="_blank" rel="noreferrer">Wiki</a></p>
  <p><a href="/#team/">Our Team</a></p>
  </>
)

const Navbar = () => {
  const [toggleMenu, setToggleMenu] = useState(false);

  return (
    <div className='mc__navbar'>
      <div className='mc__navbar-links'>
        <div className='mc__navbar-links_logo'>
          <a href="../"><img src={logo} alt="logo"/></a>
        </div>
        <div className='mc__navbar-links_container'>
          <Menu />
        </div>
      </div>
        <div className='mc__navbar-discord'>
          <a href="https://discord.gg/FgurDkv74K"><img src={discord} alt="Join our discord comunity!"/></a>
        </div>
      <div className='mc__navbar-menu'>
        {toggleMenu 
          ? <RiCloseLine color='#fff' size={27} onClick={() => setToggleMenu(false)}/>
          : <RiMenu3Line color='#fff' size={27} onClick={() => setToggleMenu(true)}/>
        }
        {toggleMenu && (
          <div className='mc__navbar-menu_container scale-up-center'>
            <div className='mc__navbar-menu_container-links'>
              <Menu />
            </div>
            <div className='mc__navbar-menu_container-links-discord'>
              <a href="https://discord.gg/FgurDkv74K"><img src={discord} alt="Join our discord comunity!"/></a>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}

export default Navbar