import React from 'react'
import './play.css';
import front from '../../assets/front.png';
import hades from '../../assets/hades.png';
import underground from '../../assets/underground.png';
import olympus from '../../assets/olympus.png';
const Play = () => {
  return (
    <div className='mc__play'>
        <div className='mc__play-content'>
          <div className='mc__play-content_join1'>
            <div className='mc__play-content_join'>
              <h1 >How To Play!</h1>
              <div className='mc__play-content_join-bar'></div>
              <p>To play on MythosCraft, you will need to own a Java Minecraft account for PC/Mac or download Minecraft from the <a href='https://www.minecraft.net/en-us'>official Minecraft website.</a></p>
              <p>The server IP address is: <div className='mc__play-content_join-link'>play.mythosmc.com</div></p>
              <div className='mc__play-content_join-notice'>
                <h2>Notice:</h2>
                <p>Currently other Minecraft versions, such as Bedrock or Console version will not work. This Includes cracked accounts/pirated copies! Keep up to date as Minecraft Bedrock support will be introduced soon! </p>
              </div>
            </div>
            <div className='mc__play-content_front'>
              <img src={front} alt="olympus"/>
            </div>
          </div>
          <div className='mc__play-content_what1'>
            <div className='mc__play-content_what'>
              <h1>What We Do</h1>
              <div className='mc__play-content_what-bar'></div>
              <p>Mythos Craft is a Greek mythology based Minecraft server where you play as the children of gods and get insane abilities that add an extra layer to the game!</p>
              <p> Fight players, custom mobs, mine new ores and clear dungeons in this brilliant new Minecraft experience!</p>
              <div className='mc__play-content_button'>
                <button><a href='https://mythoscraftmc.fandom.com/wiki/MythosCraft_Wiki'>Find out more!</a></button>
              </div>
            </div>
            <div className='mc__play-content_hades'>
              <img src={hades} alt="hades"/>
            </div>
          </div>
          <div className='mc__play-content_community1'>
            <div className='mc__play-content_community'>
              <h1>Join Our Community!</h1>
              <div className='mc__play-content_community-bar'></div>
              <p>Our discord server has an active community of over 20000 members!</p>
              <p> Join our discord to stay up to date on upcoming updates,  get support for any game related issues, meet like-minded people and discuss the wonderful world of Greek mythology with friends.</p>
              <div id='team/' className='mc__play-content_button'>
                <button><a href='https://discord.gg/FgurDkv74K'>Join our discord!</a></button>
              </div>
            </div>
            <div className='mc__play-content_olympus'>
              <img src={olympus} alt="olympus"/>
            </div>
          </div>
          <div className='mc__play-content_contact1'>
            <div className='mc__play-content_contact'>
              <h1>Our Team</h1>
              <div className='mc__play-content_contact-bar'></div>
              <p>OutragedFinn and TobbleCobble created MythosCraft late 2020!</p>
              <p>Our team includes plugin developers, moderators and administration and social media managers who all work together to provide the best player experience possible while staying true to Greek Mythology.</p>
              <p> Think you have what it takes? Apply to our team and help make MythosCraft the greatest Greek Mythology based Minecraft server ever!</p>
              <div className='mc__play-content_button'>
                <button><a href='/job-openings/'>Current job openings</a></button>
              </div>
            </div>
            <div className='mc__play-content_underground'>
              <img src={underground} alt="underground"/>
            </div>
          </div>
        </div>
    </div>
  )
}

export default Play